.email {
  position: relative;
  color: $secondary-font;
  border-bottom: 1px solid $hightlight-border;
  text-decoration: none;

  &:hover {
    color: $accent-font-additional;
    border-bottom: 1px solid $hightlight-border-additional;
  }

  &:active {
    color: $accent-font-additional;
    border-bottom: 1px solid rgba($hightlight-border-additional, 0.1);
  }
}
